<template>
  <div class="services__item">
    <PopupComponent v-if="isInfoPopupVisible" @closePopup="closeInfoPopup">
      <template v-slot:header>
        <h5>{{items_data.title}}</h5>
        <p style="font-weight: 300">{{items_data.subtitle}}</p>
      </template>
      <template v-slot:content>
        <p style="font-weight: 300">{{items_data.description}}</p>
        <p style="margin-top: 1em; font-size: 1.5em; font-weight: 300">{{items_data.price}}₽</p>
      </template>
    </PopupComponent>

    <div class="services__item__inner">
      <div class="services__item__info rounded-grey">
        <h4 class="services__item__info__title">{{items_data.title}}</h4>
        <div class="services__item__info__subtitle">{{items_data.subtitle}}</div>
      </div>
      <div class="services__item__price rounded-grey">
        <div class="services__item__price__price">Цена:</div>
        <div class="services__item__price__number">{{items_data.price}}₽</div>
      </div>
      <button class="services__item__button" @click="showPopupInfo">Подробнее</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ServicesItem",
  data: () => ({
    isInfoPopupVisible: false,
  }),
  props: {
    items_data: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  components: {
    PopupComponent: () => import("@/components/popup/PopupComponent"),
  },
  methods: {
    showPopupInfo() {
      this.isInfoPopupVisible = !this.isInfoPopupVisible;
    },
    closeInfoPopup() {
      this.isInfoPopupVisible = false;
    },
  },
};
</script>

